<script>
    import msgs from "../utils/messages";
    // eslint-disable-next-line no-unused-vars
    import axios from 'axios'

    // eslint-disable-next-line no-unused-vars

    export default {
        name: 'Login',

        data() {
            return {};
        },
        mounted() {
            if (msgs[this.$route.query.message]) {
                this.$message(msgs[this.$route.query.message])
            }
            this.createTelegramAuthScript()
            this.loadData()
        },
        methods: {
            loadData() {

            },
            createTelegramAuthScript() {
                // create script with given params
                const script = document.createElement('script')
                script.async = true
                script.src = 'https://telegram.org/js/telegram-widget.js?8'
                script.setAttribute('data-size', 'large')

                script.setAttribute('data-telegram-login', 'myAuth_test_bot')
                script.setAttribute('data-onauth', 'onTelegramAuth(user)')
                script.setAttribute('data-request-access', 'write')

                this.$refs.loginPanel.appendChild(script)

                window.onTelegramAuth = this.onTelegramAuth
            },
            async onTelegramAuth(user) {
                console.log(user)
                let isValidTelegramData = this.checkSignature(user)

                if (isValidTelegramData) {
                    if (!this.tokenDateIsExpired(user.auth_date)) {


                        this.$store.dispatch('SET_TOKEN', user)
                            .then(() => {
                                this.$router.push('/?message=auth_success')
                                this.$toast.info('auth_success')
                            });

                    } else {
                        await this.$store.dispatch('REMOVE_TOKEN')
                    }
                } else {

                    await this.$store.dispatch('REMOVE_TOKEN')
                }
            },
            checkSignature({hash, ...data}) {
                const {createHash, createHmac} = require('crypto');
                const TOKEN = "1043649899:AAExRfxdqn-bnl7cF1HaI5SAu6g4aD3lzgs";
                console.log('6666666')
                const secret = createHash('sha256')
                    .update(TOKEN)
                    .digest()

                const checkString = Object.keys(data)
                    .sort()
                    .map(k => (`${k}=${data[k]}`))
                    .join('\n');

                const hmac = createHmac('sha256', secret)
                    .update(checkString)
                    .digest('hex');

                return hmac === hash;
            },
            tokenDateIsExpired(authDate) {
                let authEndDate = (authDate + 86400) * 1000
                return Date.now() > authEndDate;
            }
        }
    }

</script>
